.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

    a {
        margin: 30px 0px;
        font-size: 17px;
        color: #687f12;
        text-decoration: none;
        font-weight: bolder;
        border-bottom: solid 2px;

        &:hover {
            transform: scale(1.05);
        }
    }
}